import { useState, useEffect } from 'react'
// import TradeDocumentRow from "./TradeDocumentRow"
import Modal from 'react-modal';
// import dummy from './dummy.pdf'
import axios from 'axios';
// import { HEADER, TRADE } from 'Pages/Trade/tradeComponent/TradeApiListFile';
import TradeNonBlockingLoader from '../tradeComponent/TradeNonBlockingLoader'
import { ToastContainer, toast } from 'react-toastify';
import { HEADER, TRADE } from '../tradeComponent/TradeApiListFile';
import { Close, CloseOutlined, FileOpen } from '@mui/icons-material';
import { Button } from '@mui/material';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'white',
        border: 'none'
    },
};
Modal.setAppElement('#root');
function TradeDocumentUpload(props) {
    const [docList, setDocList] = useState(
        [
            { docName: 'Property Document', docUrl: '/dd', docStatus: 'Rejected', docRemarks: 'Upload Proper Document' },
        ]
    )

    console.log("prop data", props.closeModalDoc)

    const [ownerId, setownerId] = useState()
    const [modalIsOpen, setIsOpen] = useState(false);
    const [modalIsOpen2, setIsOpen2] = useState(false);
    const [DocUrl, setDocUrl] = useState('')
    let subtitle;
    const [show, setshow] = useState(false);
    const [docVals, setdocVals] = useState([{ id: 1, docName: "dummy doc" }]);
    const [docName, setdocName] = useState('...');
    const [FileName, setFileName] = useState();
    const [MyFile, setMyFile] = useState();

    const openModal = () => setIsOpen(true)
    const openModal2 = () => setIsOpen2(true)
    const closeModal = () => setIsOpen(false)
    const closeModal2 = () => setIsOpen2(false)
    const afterOpenModal = () => { }
    const afterOpenModal2 = () => { }

    const modalAction = (incomingDocUrl) => {
        // alert(incomingDocUrl)
        setDocUrl(incomingDocUrl)
        openModal()
    }
    useEffect(() => {
        getRequiredDocuments()
    }, [1])

    const getRequiredDocuments = () => {
        showLoader(true);
        let id = props?.id;
        axios.post(TRADE.DOCUMENT_REQUIREMENT, { applicationId: id }, HEADER())
            .then(function (response) {
                console.log("Required documents ", response.data.data);
                if (response.data.status) {
                    setDocList(response.data.data)

                    setTimeout(() => {
                        showLoader(false);
                    }, 500);
                } else {
                    showLoader(false);

                }


            })
            .catch(function (error) {
                showLoader(false);
                console.log(error);
            })
    }

    // console.log("trade document view", props?.applicationData);

    const showLoader = (status) => {
        setshow(status);
    }

    const handleModel2 = (doc_name, values, ownerId) => {
        setdocName(doc_name);
        setdocVals(values);
        setownerId(ownerId);
        openModal2();
    }



    // const initialValue = {

    // }

    // const validationSchema
    const handleUpload = (event) => {
        // alert("hello")
        event.preventDefault();

        let licenseid = props?.id
        let formData = new FormData();
        formData.append('docRefName', docName);  //document for name
        formData.append('docMstrId', FileName);  // doucment master id
        formData.append('btn_doc_path', 0);            // Btn name
        formData.append('document', MyFile);            //File information
        formData.append('ownerId', ownerId == undefined ? "" : ownerId);   //ownerID
        formData.append('applicationId', licenseid);
        const HEADER = () => {
            let token = window.localStorage.getItem('token');
            const header = {
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                    'encType': 'multipart/form-data',
                }
            }
            return header;

        }
        console.log("ownerId", ownerId)

        axios.post(TRADE.DOCUMENT_UPLOAD, formData, HEADER())
            .then(res => {
                console.log(res.data);
                if (res.data.status) {

                    showLoader(false)
                    notify("File Uploaded Successfully", "success");
                    // props.fun(0);
                    closeModal2()
                    getRequiredDocuments()
                } else {
                    showLoader(false)
                    notify(res.data.message, "notice")

                }

            })
            .catch((error) => {
                console.log(error);
                showLoader(false);
                notify("Something went wrong", "error")
            })
    }

    const notify = (toastData, actionFlag) => {
        toast.dismiss();
        { actionFlag == 'escalated' && toast.success(toastData) }
        { actionFlag == 'success' && toast.success(toastData) }
        { actionFlag == 'de-escalated' && toast.warn(toastData) }
        { actionFlag == 'notice' && toast.warn(toastData) }
        { actionFlag == 'error' && toast.error(toastData) }
    };

    return (
        <>
            <div className="container mx-auto  ml-0  px-1 py-1 shadow-2xl shadow-sky-200 border-amber-100 rounded-lg bg-white" style={{ width: "70vw", height: "70vh" }}>
                <div className='flex justify-between bg-secondary'>
                    <h1 className='font-medium text-xl uppercase font-mono text-white  flex justify-start pl-8 py-2 rounded px-2'>
                        <img src="https://cdn-icons-png.flaticon.com/512/3121/3121601.png" alt="" className='h-12 w-12' />
                        <span className='pt-2'> &emsp;Upload Required documents </span>

                    </h1>
                    <div className='m-3'>
                        <Button variant='contained' color='warning' className='rounded-full h-8 ' onClick={() => props.closeModalDoc()}><CloseOutlined /></Button>
                    </div>
                </div>
                <div className="py-0 w-full">
                    <div className="-mx-4 sm:-mx-8 px-4 sm:px-8 py-0 overflow-x-auto">
                        <div className="inline-block min-w-full rounded-lg overflow-hidden">
                            <h1 className='font-semibold uppercase font-mono text-light'>Owner documents : </h1>
                            <table className="min-w-full leading-normal">
                                <thead className='bg-white'>
                                    <tr className='font-semibold'>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            #
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Document Name
                                        </th>

                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Owner Name
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            View
                                        </th>
                                        {/* <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Status
                                        </th> */}
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Remarks
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <TradeNonBlockingLoader show={show} />

                                    {
                                        docList?.ownersDocList?.map((items, index) => (
                                            items?.map((data) => (
                                                <>
                                                    {/* <TradeDocumentRow openModal={modalAction} docList={data} index={index} key={index}/> */}
                                                    <tr>

                                                        <td className="px-5 py-2 border-b border-gray-200  text-sm">
                                                            <p className="text-gray-900 whitespace-no-wrap">
                                                                {index + 1}
                                                            </p>
                                                        </td>
                                                        <td className="px-5 py-2 border-b border-gray-200  text-sm">
                                                            <p className="text-gray-900 whitespace-no-wrap">
                                                                {data?.docName}  <span className='text-red-500'>{data?.isMadatory == 0 ? "" : "*"}</span>
                                                            </p>
                                                        </td>
                                                        <td className="px-5 py-2 border-b border-gray-200  text-sm">
                                                            <p className="text-gray-900 whitespace-no-wrap">
                                                                {data?.ownerName}  <span className='text-red-500'></span>
                                                            </p>
                                                        </td>
                                                        <td className="px-5 py-2 border-b border-gray-200  text-sm" onClick={() => modalAction(data?.uploadDoc?.doc_path)}>
                                                            <div className="flex items-center">
                                                                <div className="flex-shrink-0">
                                                                    {data?.uploadDoc != null &&

                                                                        <a href="#" className="block relative">
                                                                            <img alt="profil" src="http://192.168.0.16:822/RMCDMC/public/assets/img/pdf_logo.png" className="mx-auto object-cover rounded-full h-10 w-10 " />
                                                                        </a>
                                                                    }
                                                                </div>
                                                            </div>
                                                        </td>
                                                        {/* <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                        <p className="text-red-900 whitespace-no-wrap">

                                                            {props?.docList?.verify_status == 1 && "Verified"}
                                                            {props?.docList?.verify_status == 2 && "Rejected"}
                                                            {props?.docList?.verify_status == 0 && "Pending"}
                                                            Pending
                                                        </p>
                                                    </td> */}
                                                        <td className="px-5 py-2 border-b border-gray-200  text-sm">
                                                            <span className="relative inline-block px-3 py-1 font-semibold text-sky-900 leading-tight">
                                                                <span aria-hidden="true" className="absolute inset-0 bg-slate-50 opacity-50 rounded-full">
                                                                </span>
                                                                {data?.uploadDoc == null ?
                                                                    <span className="relative">
                                                                        <button className='bg-sky-200 py-1.5 px-2' onClick={() => handleModel2(data?.docName, data?.docVal, data?.ownerId)}>Upload Fresh Document</button>
                                                                    </span> : <>
                                                                        <p className='font-mono text-emerald-500 opacity-50'> Document Uploaded Successfully... </p>
                                                                        <span className="relative">
                                                                            <button className='bg-slate-200 py-1.5 px-2' onClick={() => handleModel2(data?.docName, data?.docVal, data?.ownerId)}>Re - Upload Document ?</button>
                                                                        </span>
                                                                    </>
                                                                }
                                                            </span>
                                                        </td>
                                                    </tr>
                                                </>

                                            ))
                                        ))


                                    }

                                </tbody>
                            </table>

                            <h1 className='font-semibold uppercase font-mono text-gray-500'>Other documents : </h1>
                            <table className="min-w-full leading-normal">
                                <thead className='bg-white'>
                                    <tr className='font-semibold'>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            #
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Document Name
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            View
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Status
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Remarks
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <TradeNonBlockingLoader show={show} />

                                    {
                                        docList?.documentsList?.map((data, index) => (
                                            <>
                                                {/* <TradeDocumentRow openModal={modalAction} docList={data} index={index} key={index}/> */}
                                                <tr>

                                                    <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                        <p className="text-gray-900 whitespace-no-wrap">
                                                            {index + 1}
                                                        </p>
                                                    </td>
                                                    <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                        <p className="text-gray-900 whitespace-no-wrap">
                                                            {data?.docName}  <span className='text-red-500'>{data?.isMadatory == 0 ? "" : "*"}</span>
                                                        </p>
                                                    </td>
                                                    <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm" onClick={() => modalAction(data?.uploadDoc?.doc_path)}>
                                                        <div className="flex items-center">
                                                            <div className="flex-shrink-0">
                                                                {
                                                                    data?.uploadDoc != null &&
                                                                    <a href="#" className="block relative">
                                                                        <img alt="profil" src="http://192.168.0.16:822/RMCDMC/public/assets/img/pdf_logo.png" className="mx-auto object-cover rounded-full h-10 w-10 " />
                                                                    </a>
                                                                }
                                                            </div>
                                                        </div>
                                                    </td>
                                                    <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                        <p className="text-red-900 whitespace-no-wrap">

                                                            {/* {props?.docList?.verify_status == 1 && "Verified"}
                                                            {props?.docList?.verify_status == 2 && "Rejected"}
                                                            {props?.docList?.verify_status == 0 && "Pending"} */}
                                                            Accepted
                                                        </p>
                                                    </td>
                                                    <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                        <span className="relative inline-block px-3 py-1 font-semibold text-sky-900 leading-tight">
                                                            <span aria-hidden="true" className="absolute inset-0 bg-slate-50 opacity-50 rounded-full">
                                                            </span>
                                                            {data?.uploadDoc == null ?
                                                                <span className="relative">
                                                                    <button className='bg-sky-200 py-1.5 px-2' onClick={() => handleModel2(data?.docName, data?.docVal)}>Upload Fresh Document</button>
                                                                </span> : <>
                                                                    <p className='font-mono text-emerald-500 opacity-50'> Document Uploaded Successfully... </p>
                                                                    <span className="relative">
                                                                        <button className='bg-slate-200 py-1.5 px-2' onClick={() => handleModel2(data?.docName, data?.docVal)}>Re - Upload Document ?</button>
                                                                    </span>
                                                                </>
                                                            }
                                                        </span>
                                                    </td>
                                                </tr>
                                            </>

                                        ))


                                    }

                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >

                <div class=" rounded-lg shadow-xl border-2 border-gray-50 ml-32 px-0" style={{ 'width': '70vw', 'height': '80vh' }}>
                    <iframe className='w-full h-full' src={DocUrl} frameborder="0"></iframe>

                </div>

            </Modal>

            <Modal
                isOpen={modalIsOpen2}
                onAfterOpen={afterOpenModal2}
                onRequestClose={closeModal2}
                style={customStyles}
                contentLabel="Example Modal2"
            >

                <div class=" rounded-lg shadow-xl border-2 bg-slate-50  px-0" style={{ 'width': '40vw', 'height': '20vh' }}>
                    {/* <iframe className='w-full h-full' src={dummy} frameborder="0"></iframe> */}
                    {/* hello */}

                    <form encType="multipart/form" onSubmit={handleUpload} >
                        <div className='text-center grid grid-cols-1 md:grid-cols-4 gap-1 p-1 font-mono text-xs md:text-[10px'>
                            <label htmlFor="" className=' col-span-1 font-bold text-center'>{docName} : </label>
                            <select name={`${docName}`} id="owner_doc" className='px-4 py-1.5 border rounded bg-white' onChange={(e) => setFileName(e.target.value)}>
                                <option value="">SELECT</option>
                                {docVals?.map((item, index) => (
                                    <option value={item?.id} key={index}>{item?.doc_name}</option>
                                ))}

                            </select>
                            {/* <input type="hidden" value={item?.id} name="" /> */}
                            <input type="file" name='file' id='file' className='px-3 py-1.5 border rounded bg-white' onChange={(e) => setMyFile(e.target.files[0])} />

                        </div>
                        <button type='submit' className='px-3 py-1.5 float-right m-4 bg-fuchsia-500 text-slate-100 rounded-sm uppercase text-xs'>Upload document</button>
                        <samll className='text-amber-600 text-xs mt-4 text-center capitalize hidden md:block'>You can upload any one of the document listed in the select box</samll>
                    </form>

                </div>

            </Modal>
        </>
    )
}

export default TradeDocumentUpload
/**
 * Exported to :
 * 1. PropertySafDetailsTabs Component
 * 
 */