import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { GrFormClose } from "react-icons/gr";
import { HiCurrencyRupee } from "react-icons/hi";
import axios from "axios";
import { HEADER, TRADE } from "../tradeComponent/TradeApiListFile";
import RazorpayPaymentScreen from "../../../Components/RazorpayPaymentScreen";
import { useNavigate } from "react-router-dom";


const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
        background: "transparent",
        border: "none",
    },
};

function TradePaymentModal(props) {
    // Modal.setAppElement('#yourAppElement');
    const [modalIsOpen, setIsOpen] = React.useState(false);

    const navigate = useNavigate();

    console.log("order data", props?.OrderResponse)
    const handleDelete = (id) => {
        console.log("ID to be deleted", id);
    };

    useEffect(() => {
        if (props.openPaymentModal > 0) setIsOpen(true);
    }, [props.openPaymentModal]);

    function afterOpenModal() { }

    function closeModal() {
        setIsOpen(false);
        // props.refetchListOfRoles();
    }

    const dreturn = (data) => {   // In (DATA) this function returns the Paymen Status, Message and Other Response data form Razorpay Server
        console.log('Payment Status =>', data.data)
        props.showLoader(true);

        if (data.status) {
            props.toast(data.message, "success");

            setTimeout(() => {
                confirmTradePayment(data.data);
            }, 10000)
        } else {
            props.toast(data.message, "error")
            props.showLoader(false);

        }
    }

    const confirmTradePayment = (data) => {
        console.log("data", data);
        axios.post(TRADE.CONFIRM_TRADE_ONLINE_PAYMENT, { orderId: data?.razorpay_order_id, paymentId: data?.razorpay_payment_id }, HEADER())
            .then((response) => {
                console.log("response for payment confirmation", response);
                // if (response.data.data.status) {
                props.showLoader(false);
                if (response.data.status) {
                    navigate('/trade-payment-receipt/' + props?.id + "/" + response?.data?.data?.transectionId, { replace: true });
                } else {
                    props.toast("Payment is done but it has not updated in our servers yet...kindly wait for some time", "notice");
                    navigate('/trade-payment-receipt/' + props?.id + "/" + response?.data?.data?.transectionId, { replace: true });


                }
                // }
            })
            .catch((error) => {
                console.log(error);
            })
    }


    const payNow = () => {
        console.log("Clicked..")
        props.showLoader(true);
        const payload = {
            licenceId: props?.id
        }

        // axios.post('http://192.168.0.16:8000/api/trade/payOnline', payload, HEADER())
        //     .then((res) => {
        //         console.log("Oder ID Generated", res)

        //         if (res.data.status == true) {
        let response = RazorpayPaymentScreen(props?.OrderResponse, dreturn)

        const promise1 = Promise.resolve(response);

        promise1.then((value) => {
            console.log("payment response", value);
            // expected output: 123
        });
        // console.log("payment response", response);
        //     }
        // })
        // .catch((err) => console.log("Error genrating order id", err))
        setTimeout(() => {
            closeModal();
            props.showLoader(false);
        }, 500);

    }



    return (
        <div className="">

            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div className="bg-gradient-to-tr from-cyan-300 to-teal-600 text-slate-50 shadow-2xl border border-sky-200 p-5 m-5 rounded-md"  >
                    <div className=''>
                        <div className=' border-b-2 py-1 pl-3 font-semibold border-red-400 flex'><HiCurrencyRupee size={20} className='mt-0.5 mr-1' />Make Payment For {props?.OrderResponse?.application_no}</div>

                        <div className="md:flex block w-full h-56">
                            <div className='grid grid-cols-12 px-8 pt-3 leading-8 '>
                                <div className='md:col-span-6 col-span-12'>
                                    <div className='grid grid-cols-12'>
                                        <div className='col-span-6 font-semibold'>
                                            <p>Firm Name</p>
                                            <p>Ward No</p>
                                            <p>Application Type</p>
                                            <p>License For Years</p>
                                            <p>Apply Date</p>
                                        </div>
                                        <div className='col-span-6'>
                                            <p>{props?.OrderResponse?.firmName || <span className="text-red"> John Doe</span>}</p>
                                            <p>{props?.OrderResponse?.wardNo}</p>
                                            <p>{props?.OrderResponse?.applicationType}</p>
                                            <p>&emsp;{props?.OrderResponse?.licenceForYears} year <small>(s)</small></p>
                                            <p>{props?.OrderResponse?.applyDate == null ? JSON.stringify(new Date()).slice(1, 11) : props?.OrderResponse?.applyDate}</p>
                                        </div>
                                    </div>
                                </div>
                                <div className='md:col-span-6 col-span-12'>
                                    <div className='grid grid-cols-12'>
                                        <div className='col-span-6 font-semibold'>
                                            <p>Name</p>
                                            <p>New Ward No</p>
                                        </div>
                                        <div className='col-span-6'>
                                            <p>{props?.OrderResponse?.name}</p>
                                            <p>{props?.OrderResponse?.newWardNo}</p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                            <div className='border-l text-center'>
                                <div className="my-8">
                                    <h1 className="font-bold text-3xl"> {props?.OrderResponse?.amount}</h1>
                                    <h1 className="font-bold text-sm ">Payable Amount</h1>
                                </div>
                                <div className='flex justify-center'>
                                    <button onClick={closeModal} className='mx-2 bg-red-600 hover:bg-red-700 transition duration-200 hover:scale-105 font-normal text-white px-6 py-1 text-lg  rounded-sm shadow-xl'>Cancel</button>
                                    <button onClick={payNow} className='mx-2 bg-indigo-600 hover:bg-indigo-700 transition duration-200 hover:scale-105 font-normal text-white px-3 py-1 text-lg  rounded-sm shadow-xl'>Pay Now</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
        </div>
    );
}

// ReactDOM.render(<App />, appElement);

export default TradePaymentModal;
